<template>
  <div class="layout">
    <Layout>
      <Header>
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo">welcome to my blog</div>
          <div class="layout-nav">
            <MenuItem name="1" class="iconText" @click.native="jump('codes')">
              <Icon type="ios-navigate"></Icon>
              codes
            </MenuItem>
            <MenuItem name="2" class="iconText" @click.native="jump('picture')">
              <Icon type="ios-keypad"></Icon>
              picture
            </MenuItem>
            <MenuItem name="3" class="iconText" @click.native="jump('music')">
              <Icon type="ios-analytics"></Icon>
              music
            </MenuItem>
            <MenuItem name="4" class="iconText" @click.native="jump('text')">
              <Icon type="ios-paper"></Icon>
              text
            </MenuItem>
          </div>
        </Menu>
      </Header>

      <Content :style="{ padding: '0 50px' }">
        <Breadcrumb :style="{ margin: '20px 0' }">
          <BreadcrumbItem class="bread"
            >This blog is created by treeyerr</BreadcrumbItem
          >
        </Breadcrumb>
        <Card>
          <div class="content">
            <div class="time">{{ nowDate }} <br />{{ nowTime }}</div>
          </div>
        </Card>
      </Content>
      <Footer class="layout-footer-center">2022/7/12 &copy; Treeyerr</Footer>
    </Layout>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      nowDate: null,
      nowTime: null,
    };
  },
  created() {
    this.nowDate = this.handleDateChange();
    setInterval(() => {
      this.nowTime = this.getNowTimes();
    }, 1000);
  },
  methods: {
    jump(type) {
      console.log(type);
      const pathObj = {
        codes: "/codes",
        picture: "/picture",
        text: "/text",
        music: "/music",
      };
      const path = pathObj[type] || "/home";
      this.$router.push({ path });
    },
    handleDateChange() {
      let date = new Date();
      let year = date.getFullYear();
      let mouth = date.getMonth() + 1;
      let dates = date.getDate();
      let arr = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      let day = date.getDay();
      return `${year}年${mouth}月${dates}日 ${arr[day]}`;
    },
    getNowTimes() {
      let time = new Date();
      let h = time.getHours();
      let m = time.getMinutes();
      let s = time.getSeconds();
      h = h < 10 ? `0${h}` : h;
      m = m < 10 ? `0${m}` : m;
      s = s < 10 ? `0${s}` : s;
      return `${h}:${m}:${s}`;
    },
  },
};
</script>

<style scoped>
.layout {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}
.layout-logo {
  width: 260px;
  height: 50px;
  /* background: #5b6270; */
  border-radius: 3px;
  float: left;
  position: relative;
  top: 15px;
  left: 20px;
  font-size: 26px;
  color: #fff;
  text-align: center;
  line-height: 50px;
}
.layout-nav {
  width: 500px;
  margin: 0 auto;
  margin-right: 20px;
}
.layout-footer-center {
  text-align: center;
}
.content {
  display: flex;
  justify-content: center;
  height: 800px;
  background: url(../../../static/image/img.png) 30% 90%;
}
.bread {
  font-size: 23px;
}
.iconText {
  font-size: 20px;
}
.time{
    color: #ffffff;
  font-size: 35px;
  font-weight: bold;
  margin-top: 50px;
  text-align: center;
}
</style>




